import List from './list.vue'
const pickerData = [
  { text: '流畅', value: '360' },
  { text: '标清', value: '480' },
  { text: '高清', value: '720' },
  { text: '超清', value: '1080' }
]
const videoParams = [
  { index: 0,  },
  { index: 1,  },
  { index: 2,  },
  { index: 3,  }
]

const r = 1
const merge = _.merge([], pickerData, videoParams)
const videoType = merge[0]

export default {
  name: '',
  components: { List },
  data() {
    return {
      mvList: [],
      merge,
      showFlag: false,
      videoParams,
      error: false
    }
  },
  props: {
    label: {
      type: Object,
      default: () => ({})
    },
    query: {
      type: Object,
      default: () => ({})
    }
  },
  activated() {
    this.showFlag = true
  },
  deactivated() {
    this.showFlag = false
  },
  created() {
    // this.
    this.initData()
    this.$emit('created')
  },
  mounted() {
    $(document).bind(
      'fullscreenchange webkitfullscreenchange mozfullscreenchange',
      function() {
        //todo code
        // console.log(document.webkitIsFullScreen)
      }
    )
  },
  methods: {
    async getMvList() {
      var { mv_list = [], code } = await this.__getJson(
        this.__HOT_MV_LIST_URL,
        this.query
      ).catch(err => {
        console.error(err)
        this.error = true
      })
      if (code == this.__QERR_OK) {
        // console.log(mv_list)

        const { list } = mv_list.data
        const vids = list.map(item => item.vid)
        const {
          getMvUrl: { data: urlData }
        } = await this.__getJson('/getMvPlayUrl', { vids })

        return list.map(item => {
          item.videoType = this.__clone__(videoType)
          item.singer = this.__format(item.singers)
          item.url = urlData[item.vid].mp4[1].freeflow_url[0]
          item.urls = urlData[item.vid].mp4
            .filter((item, index) => index > 0)
            .map(item => item.freeflow_url[0])
          return item
        })
      } else {
        // console.log(data)
      }
    },
    async initData() {
      this.mvList = await this.getMvList()
    },
    async addData(list) {
      this.mvList.push(...(await this.getMvList()))
    }
  }
}



